import React, { Component } from 'react';
import phoneSeat from './image/phoneSeat.jpg'
import Sboard from './image/Sboard.jpg'
import pic3 from './image/card.jpg'
import pic4 from './image/calend2.jpg'
import pic5 from './image/plate.jpg'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {Card, CardMedia, CardTitle, CardText} from 'material-ui/Card';

import './App.css';

class Cards extends Component {
  render() {
    return (
      <MuiThemeProvider>
      <div className="App">
	      <div className="CardStyle">
		      <Card zDepth={2}>
		      	<CardTitle title="手绘日历" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>
				    <CardMedia expandable={true}>
				      <img src={pic4} alt="phoneSeat" />
				    </CardMedia>

			  	</Card> 
	      </div>

	      <div className="CardStyle">
		      <Card zDepth={2}>
			    <CardTitle title="黑胡桃木手机座" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

			    <CardMedia expandable={true}>
			      <img src={phoneSeat} alt="PhoneSeat" />
			    </CardMedia>

			 	  </Card>
	      </div>

      	<div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="上海科技大学牌匾" subtitle="非卖品" actAsExpander={true} showExpandableButton={true} />

				    <CardMedia expandable={true}>
				      <img src={Sboard} alt="sboard" />
				    </CardMedia>
				    <CardText>
				      Lorem ipsum dolor sit amet, consectetur adipiscing elit.
				      Donec mattis pretium massa. Aliquam erat volutpat. Nulla facilisi.
				      
				    </CardText>
			  	</Card>			  
	      </div>

	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="手机支撑片" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

				    <CardMedia expandable={true}>
				      <img src={pic3} alt="phoneSeat" />
				    </CardMedia>

			  	</Card>
	      </div>

	      <div className="CardStyle">
		      <Card zDepth={2}>
				    <CardTitle title="迷你托盘" subtitle="接受预定" actAsExpander={true} showExpandableButton={true}/>

				    <CardMedia expandable={true}>
				      <img src={pic5} alt="phoneSeat" />
				    </CardMedia>

			  	</Card>
	      </div>

      </div>
      </MuiThemeProvider>
    );
  }
}

export default Cards;



