import React, { Component } from 'react';
import logo from './image/logo.png';
import Drawer from 'material-ui/Drawer';
import AppBar from 'material-ui/AppBar';
import MenuItem from 'material-ui/MenuItem';
import Menu from 'material-ui/Menu'
import Paper from 'material-ui/Paper';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import Cards from './Cards';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      Dopen: false,
    };
  };


  handleOpen = () => {
    this.setState({Dopen: true});
  };

  handleDClose = () => {
    this.setState({Dopen: false});
  };

  handleToggle = () => this.setState({open: !this.state.open});

  handleClose = () => this.setState({open: false});
  render() {
     const actions = [
      <FlatButton
        label="朕知道了"
        primary={true}
        onClick={this.handleDClose}
      />,
    ];
    return (
      <MuiThemeProvider>
      <div className="App">

        <Drawer
          docked={false}
          width={"80%"}
          open={this.state.open}
          onRequestChange={(open) => this.setState({open})}
        > 
          <Paper className="App-header" zDepth={2} style={{backgroundColor: "#331e17"}}>
            <img src={logo} className="App-logo" alt="logo" />
            <h1 className="App-title">Wood Theory</h1>
          </Paper>
          <Menu desktop={true} style={{textAlign:'left'}}>

          <MenuItem onClick={this.handleClose}>关于木理坊</MenuItem>
          <MenuItem onClick={this.handleClose} onClick={this.handleOpen}>预定方法</MenuItem>

          </Menu>
        </Drawer>
        <Dialog
          title="预定方法"
          actions={actions}
          modal={true}
          open={this.state.Dopen}
        >
          用你的上科大邮箱发送预定信息至xxxx邮箱。
        </Dialog>
        <AppBar
          title="Wood Theory" zDepth={2}
          iconClassNameRight="muidocs-icon-navigation-expand-more"
          style={{backgroundColor: "#331e17"}}
          onLeftIconButtonTouchTap={this.handleToggle}
        />
       

        <Cards/>

      </div>      
      </MuiThemeProvider>
    );
  }
}

export default App;



